.wrapper{
    width: 100%;
}
.content{
    width: 100%;
    display:flex;

}
.sibar{
    width: 30%;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-right: 40px;
}

.tabs{
    width: 100%;
    .tab-item{
        display:flex; 
        align-items: cebnter;
        justify-content: center;
        width: 100%;
        padding:10px 18px;
        border-radius: 30px;
        margin-bottom: 20px;
        cursor:pointer;
        &.active {
        background-color: #E8E8E8;
        }
    }
}
.content-tab{
    width: 69%;
    padding:0 0 0 20px;
}
.information{
    width: 100%;
    display: flex;
    flex-direction: column;
    .avt{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        margin-right: 20px;
    }
}
.form-group{
    width: 46%;
    display: flex;
    flex-direction: column;
    >label{
        font-size: 13px;
        color:#263646;
        font-weight: 300;
        margin-bottom: 4px;
        margin-left:8px;
    }
    >input{
        outline: none;
        border:1px solid #263646;
        padding:10px 16px;
        border-radius: 20px;
        font-size: 14px;
        background-color: transparent;
    }
}
.btn-submit{
    width:30%;
    color:white;
    border-radius: 30px;
    padding:8px 0;
    background-color: #263646;
    font-size: 14px;
    font-weight: 400;
    margin:auto;
}

.bill{
    width: 100%;
    padding:2px 0 0 30px; 
}
.order{
    width: 100%;
    padding:2px 0 0 30px; 
}
.bill-item{
    width:100%;
    padding:20px;
    display:flex;
    flex-direction: column;
        border-radius: 30px;
        background-color: #fff;
        box-shadow: 0 0 3px rgba(0,0,0,0.3);
    margin-bottom: 10px;
    }
@media screen and (max-width: 639px) {
    .content{
        flex-wrap: wrap;
    }
    .sibar{
        width: 100%;
    }
    .content-tab{
        width: 100%;
        padding:0;
    }
    .bill , .order{
        padding:0;
    }
}